* {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  line-height: 150%;
  /* 67.5px */
  font-style: normal;
}

.big-text {
  font-size: 45px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: inherit;
}

h1, .h1 {
  color: #FFF;
  font-size: 60px;
  font-weight: 700;
  line-height: 140%;
  /* 84px */
  text-transform: uppercase;
  letter-spacing: inherit;
}

h2, .h2 {
  color: #181717;
  font-size: 40px;
  font-weight: 600;
  line-height: 116%;
  /* 46.4px */
  letter-spacing: inherit;
  margin-bottom: 35px;
}

h3, .h3, .h3_subname {
  color: #181717;
  font-size: 20px;
  font-weight: 700;
  line-height: 160%;
  /* 32px */
  letter-spacing: inherit;
}

p, .p {
  font-variant-numeric: stacked-fractions;
  font-feature-settings: "hlig" on, "dlig" on;
  font-size: 16px;
  font-weight: 500;
  line-height: 151.278%;
  /* 24.205px */
  letter-spacing: 0.16px;
}

.normal_text-gray {
  color: #525252;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: inherit;
}

.normal_text {
  color: #181717;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: inherit;
}

.normal_text-heading {
  color: #525252;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: inherit;
}

.accent {
  color: #57C645;
}

.small_text {
  color: #181717;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  /* 22.4px */
}
.small_text__bold {
  font-weight: 600;
  font-size: 14px;
}

@media screen and (max-width: 1300px) {
  .big-text {
    font-size: 41px;
    line-height: 135%;
  }
}
@media screen and (max-width: 1300px) {
  .big-text {
    font-size: 20px;
  }
}
@media screen and (max-width: 760px) {
  .normal_text, .normal_text-gray {
    font-size: 14px;
  }

  h2, .h2 {
    font-size: 32px;
  }
}
@media screen and (max-width: 360px) {
  .h2, h2 {
    font-size: 29px;
  }
}
html {
  font-size: 100%;
}

body {
  font-family: "Montserrat", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: #181717;
  background-color: #fff;
}

.container {
  max-width: 1255px;
  padding: 0 13px 0px 15px;
  margin: 0 auto;
}

.three-row {
  display: flex;
  justify-content: space-between;
  flex-flow: wrap;
}
.three-row .row, .three-row .item {
  width: 33.3%;
}

.card {
  transition: all ease 0.3s;
  border-radius: 15px;
  padding: 0px 15px 15px;
}
.card .card-img {
  width: 385px;
  height: 425px;
  display: flex;
  align-items: center;
}
.card .card-img img {
  object-fit: cover;
  width: 100%;
}
.card .card-content {
  padding-left: 16px;
  padding-top: 5px;
}
.card .card-content a {
  transition: all ease 0.3s;
  display: block;
}
.card .card-content a:hover {
  text-decoration: underline;
}
.card .small_text {
  margin-bottom: 15px;
}
.card:hover {
  box-shadow: -6px 7px 30px #e2e2e2;
}

.sizes_block {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 9px;
}
.sizes_block .size {
  width: 52px;
  height: 52px;
  background-color: #F7F7F7;
  border-radius: 100%;
  display: flex;
  align-items: center;
  align-self: center;
  font-family: inherit;
  color: #181717;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 160%;
  /* 25.6px */
  text-align: center;
  justify-content: center;
  margin-right: 14px;
  transition: all ease 0.3s;
  margin-bottom: 10px;
}
.sizes_block .size:hover, .sizes_block .size.active {
  color: #fff;
  background-color: #57C645;
}

.characters-table {
  padding: 0px 40px 10px 0px;
}
.characters-table .line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 43px;
  padding: 6px 5px 7px 0;
}
.characters-table .line:not(:last-child) {
  border-bottom: 1px solid #D1D1D1;
}
.characters-table .small_text {
  margin-bottom: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: none;
  white-space: nowrap;
  width: 80%;
}

.new-slider {
  position: relative;
  margin-bottom: 80px;
}
.new-slider .slide {
  background-color: #e2e2e2;
  margin: 20px;
  width: 1255px;
  height: 686px;
  overflow: hidden;
  position: relative;
}
.new-slider .wrap-main-img {
  width: 100%;
  height: 100%;
}
.new-slider .wrap-main-img img {
  width: 100%;
  height: 100%;
  display: block;
  margin-right: 0px;
  object-fit: cover;
}

.slick-slider {
  text-align: center;
  /* Центрування тексту для блока */
}

.slick-slide {
  display: inline-block;
  /* Робить слайди блоками, які ідуть в ряд */
  vertical-align: top;
  /* Вирівнювання вертикально */
}
.slick-slide img {
  width: 100%;
}

.slick-center .slick-slide {
  width: 1255px !important;
  transition: transform 0.3s ease;
  /* Плавний перехід масштабу */
}

.slick-current.slick-center {
  margin: 17px 31px 20px 59px;
  width: 1207px !important;
}

.nav-block {
  position: absolute;
  top: 744px;
  right: 24px;
  display: flex;
}

.custom-nav {
  z-index: 2 !important;
  width: 40px !important;
  height: 40px !important;
  transition: all ease 0.3s;
  background-color: #F5F5F5;
  z-index: 99;
  cursor: pointer;
  display: flex;
}
.custom-nav img {
  margin: 0 auto;
  width: 7px;
}
.custom-nav.custom-prev-button {
  right: 64px;
}
.custom-nav:hover {
  opacity: 0.6;
}
.custom-nav .custom-next-button {
  right: -17px;
  background-position: center;
  background-color: #F5F5F5;
}

.gallery-block {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 80px;
}

.left-gallery {
  position: relative;
  width: 65%;
}

.left-gallery .nav-block {
  top: auto;
  bottom: 0px;
  right: 0px;
}

.right-gallery {
  width: 385px;
}
.right-gallery .video-gallery {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.video-description {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 151.278%;
  /* 24.205px */
  letter-spacing: 0.16px;
  transition: all ease 0.3s;
}
.video-description:hover {
  text-decoration: underline;
}

.video-wrap {
  margin-bottom: 15px;
}
.video-wrap iframe {
  width: 100%;
  height: 167px;
}

.slider-left .wrap-main-img {
  height: 481px;
}
.slider-left .wrap-main-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slick-track {
  transition: transform 1s;
}

.slick-slide {
  transition: width 0.3s;
}

.slick-dots {
  display: flex;
  justify-content: flex-start;
  width: 1200px;
  margin: 0 auto;
}
.slick-dots li {
  width: 172px;
  height: 3px;
  color: #000;
  font-size: 0px;
  background-color: #ccc;
  cursor: pointer;
}
.slick-dots li.slick-active {
  background-color: #181717;
}

.full-screen-block {
  margin-bottom: 100px;
}
.full-screen-block h2, .full-screen-block .h2 {
  margin-bottom: 35px;
  padding-left: 4px;
}
.full-screen-block .container {
  position: relative;
}

.news-block {
  display: flex;
  position: absolute;
  bottom: 57px;
  left: 105px;
  z-index: 9;
  justify-content: flex-start;
  align-items: flex-end;
  color: #F5F5F5;
}
.news-block p {
  color: #fff;
}
.news-block .new-name {
  margin-bottom: 25px;
  color: #F5F5F5;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  /* 18.9px */
  text-align: left;
}
.news-block .new-header {
  margin-bottom: 26px;
  color: #F5F5F5;
  font-size: 67.087px;
  font-style: normal;
  font-weight: 700;
  line-height: 105%;
  /* 70.442px */
  text-align: left;
  text-align: left;
}
.news-block .new-subscribtion {
  color: #F5F5F5;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 149%;
  /* 26.82px */
}
.news-block .part-left {
  text-align: left;
  max-width: 60%;
}
.news-block .part-right {
  padding-bottom: 39px;
  padding-left: 38px;
}
.news-block .news-link {
  display: flex;
  align-items: center;
  transition: all ease 0.3s;
  color: #F5F5F5;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  /* 28px */
}
.news-block .news-link span {
  margin-right: 30px;
}
.news-block .news-link:hover {
  transform: scale(1.1);
}

.button {
  display: flex;
  width: 100%;
  height: 56px;
  padding: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: #FFF;
  background: #57C645;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: all ease 0.3s;
}
.button:hover {
  background-color: #3eb72b;
}

.grey-bg {
  background-color: #F5F5F5;
  padding: 80px 0px;
}

.three-in-row-wrap {
  display: flex;
  flex-flow: wrap;
}
.three-in-row-wrap .item {
  margin-bottom: 47px;
  margin-right: 34px;
  width: 386px;
}
.three-in-row-wrap .item:nth-child(3n) {
  margin-right: 0px;
}
.three-in-row-wrap .wrap-item-image {
  width: 100%;
  height: 289px;
  margin-bottom: 20px;
  overflow: hidden;
}
.three-in-row-wrap .wrap-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all ease 0.3s;
}
.three-in-row-wrap .wrap-item-image:hover img {
  transform: scale(1.1);
}
.three-in-row-wrap .item-subheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.three-in-row-wrap .p.item-subheader {
  margin-bottom: 3px;
}
.three-in-row-wrap .full-text {
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 160%;
  /* 32px */
  transition: all ease 0.3s;
}
.three-in-row-wrap .full-text:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1300px) {
  .card .card-img {
    width: 100%;
    height: 272px;
  }

  .three-in-row-wrap .item {
    margin-right: 16px;
    width: 346px;
  }
}
@media screen and (max-width: 1118px) {
  .three-in-row-wrap .item {
    margin-right: inherit;
    width: 40%;
  }

  .three-in-row-wrap .item:nth-child(3n) {
    margin-right: inherit;
  }

  .three-in-row-wrap {
    justify-content: space-evenly;
  }
}
@media screen and (max-width: 768px) {
  .container {
    padding: 0 22px 0 20px;
  }

  .three-row {
    flex-direction: column;
    align-items: center;
  }
  .three-row .row, .three-row .item {
    width: 100%;
  }

  .card {
    padding: 0px 5px;
  }

  .card .card-img {
    height: auto;
    width: 100%;
    margin: 81px 0px 64px;
  }

  .card .card-content {
    padding-left: 0px;
  }

  .characters-table {
    padding: 0px 0px 10px 0;
  }

  .characters-table .line {
    height: 39px;
    padding: 5px 5px 5px 0;
  }

  .grey-bg {
    padding: 40px 0px;
    margin-bottom: 40;
  }

  .three-in-row-wrap .wrap-item-image {
    height: 265px;
    width: 265px;
  }

  .three-in-row-wrap {
    justify-content: space-evenly;
  }

  .three-in-row-wrap .item-name {
    font-size: 12px;
  }

  .three-in-row-wrap .wrap-item-image {
    height: 270px;
    width: 100%;
  }

  .three-in-row-wrap .item {
    width: 100%;
  }

  .three-in-row-wrap .wrap-item-image {
    height: 300px;
    width: 100%;
  }

  .last-news {
    margin-bottom: 100px;
    padding-top: 40px;
  }
}
@media screen and (max-width: 400px) {
  .three-in-row-wrap {
    justify-content: space-between;
  }

  .three-in-row-wrap .wrap-item-image {
    height: 160px;
    width: 160px;
  }

  .three-in-row-wrap .item {
    margin-bottom: 17px;
  }

  .portfolio {
    padding: 30px 0;
  }

  .three-in-row-wrap .wrap-item-image {
    height: 220px;
    width: 100%;
  }

  .check:before {
    height: 17px;
    left: 2px;
    top: 5px;
    width: 17px;
  }
}
@media screen and (max-width: 340px) {
  .container {
    padding: 0 15px 0 15px;
