/* Dropup Button */
.dropup {
  position: relative;
}
.dropup::after {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  width: 12px;
  background-color: #fff;
  right: -20px;
  background-position: right;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%);
}
.dropup::before {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  width: 12px;
  background-color: #fff;
  right: -20px;
  background-position: right;
  background-repeat: no-repeat;
  top: 50%;
  transition: all ease 0.3s;
  transform: translateY(-50%) rotate(90deg);
}
.dropup:hover::after, .dropup:hover::before, .dropup.active::after, .dropup.active::before {
  background-color: #57C645;
}
.dropup:hover, .dropup.active {
  color: #57C645;
}
.dropup.open:before {
  transform: translateY(-50%) rotate(0deg);
}

.menu li {
  transition: all ease 0.3s;
}

.menu li.active {
  color: #57C645;
}

.menu li:hover a {
  color: #57C645;
}

.menu-item-has-children.dropup.active a:nth-child(1) {
  color: #57C645;
}

.menu-item-has-children.dropup.active .sub-menu a,
.menu li:hover .sub-menu a {
  color: #fff;
}

.menu-item-has-children.dropup.active .sub-menu li.active a,
.menu li:hover .sub-menu a:hover {
  color: #57C645;
}

.menu-item-has-children.dropup .sub-menu a::after {
  display: none;
}

.menu-item-has-children.open .sub-menu {
  display: block;
  opacity: 1;
  pointer-events: all;
}

/* The container <div> - needed to position the dropup content */
.dropup {
  position: relative;
  display: inline-block;
}

/* Dropup content (Hidden by Default) */
.sub-menu {
  opacity: 0;
  transition: all ease 0.3s;
  pointer-events: none;
  position: absolute;
  top: 10px;
  padding-top: 20px;
  background-color: transparent;
  min-width: 230px;
  z-index: 1;
  color: #fff;
}
.sub-menu a {
  color: #fff;
}

.menu ul.sub-menu li {
  margin-right: 0px;
}

/* Show the dropup menu on hover */
/* Change the background color of the dropup button when the dropup content is shown */
